html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #252525; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

body {
  color: #ffffff;
  background-color: #000000;
  font-family: helvetica, sans-serif;
  font-size: 16px;
  font-weight: 100;
  margin: auto;
  max-width: 1200px; }

.title {
  margin: 0; }
  .title--main {
    font-family: 'BioRhyme', serif;
    font-size: 45px;
    color: #ffffff;
    height: 100%; }
    .title--main:hover {
      color: #000000;
      background-color: #ffffff; }
    @media (max-width: 767px) {
      .title--main {
        font-size: 30px; } }
  .title--tagline {
    font-family: 'Zilla Slab Highlight', cursive;
    font-size: 70px;
    text-align: right; }
    @media (max-width: 767px) {
      .title--tagline {
        font-size: 50px; } }
  .title--section {
    font-weight: 100;
    font-size: 30px; }
    @media (max-width: 767px) {
      .title--section {
        font-size: 24px; } }

.header {
  -webkit-display: flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding: 0 20px;
  border-bottom: 3px solid #ffffff; }
  .header__nav {
    -webkit-display: flex;
    -webkit-align-items: center;
    display: flex;
    align-items: center;
    margin-right: -3px; }
    .header__nav__menu {
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 76px;
      right: 0;
      z-index: 1;
      padding: 0 16px;
      background-color: #000000; }
      @media (max-width: 383px) {
        .header__nav__menu {
          top: 97px;
          width: 100%; } }
    @media (max-width: 767px) {
      .header__nav--desktop {
        display: none; } }
    .header__nav--mobile {
      display: none; }
      @media (max-width: 767px) {
        .header__nav--mobile {
          display: flex; } }

.button {
  color: #000000;
  font-size: 20px;
  margin: 15px 3px;
  padding: 10px 15px;
  text-decoration: none;
  text-align: center; }
  @media (max-width: 767px) {
    .button {
      font-size: 16px; } }
  .button:hover {
    background-color: #ffffff;
    cursor: pointer; }
  .button--yellow {
    background-color: #f1e651; }
  .button--mint {
    background-color: #7acece; }
  .button--red {
    background-color: #f5430b; }
  .button--full {
    width: 100%;
    margin: 15px 0; }

.footer {
  border-top: 3px solid #ffffff;
  padding: 10px 20px;
  font-size: 12px; }

.section {
  -webkit-display: flex;
  -webkit-align-items: center;
  -webkit-flex-direction: column;
  -webkit-justify-content: space-between;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 20px; }
  .section--narrow {
    max-width: 500px;
    margin: auto; }
  .section--top {
    -webkit-align-items: flex-end;
    -webkit-justify-content: center;
    align-items: flex-end;
    justify-content: center;
    background-image: url("../img/fireworks.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 300px; }
    @media (max-width: 767px) {
      .section--top {
        height: 200px; } }
  .section--border-top {
    border-top: 3px solid #f5430b; }

.row {
  -webkit-display: flex;
  -webkit-justify-content: space-between;
  display: flex;
  justify-content: space-between;
  margin: 10px auto;
  max-width: 900px;
  width: 100%; }
  .row--title {
    align-items: center; }
  @media (max-width: 767px) {
    .row--mobile-col {
      -webkit-align-items: center;
      -webkit-flex-direction: column;
      align-items: center;
      flex-direction: column; } }

.thumbnail {
  width: 200px;
  height: 200px; }
  .thumbnail--avatar {
    border-radius: 50%; }

.thumbnail-container {
  margin-right: 30px;
  max-width: 200px; }
  @media (max-width: 767px) {
    .thumbnail-container {
      margin: 0 0 15px; } }

.column-container {
  display: flex;
  flex-direction: column;
  align-items: center; }

.text {
  background-color: #252525;
  padding: 0 20px;
  margin: 0;
  flex-grow: 0; }
  .text--grow {
    flex-grow: 1; }

.accordion {
  width: 100%;
  padding: 0 15px;
  margin: 15px 0 0;
  background-color: #7acece;
  color: #000000;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .accordion:hover {
    background-color: #ffffff; }
  .accordion__title {
    font-weight: normal; }
  .accordion__close {
    background-image: url("../img/plus-icon.png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 30px;
    width: 30px;
    transform: rotate(45deg);
    transition-duration: 0.3s; }
  .accordion--yellow {
    background-color: #f1e651; }
  .accordion--red {
    background-color: #f5430b; }

.bullets {
  padding-left: 16px; }

.skills-block {
  display: flex;
  flex-direction: column;
  flex-basis: 33%;
  width: 100%;
  max-width: 300px;
  margin-top: 0; }
  .skills-block__list {
    list-style: none;
    padding: 15px 0;
    margin: 0;
    flex-grow: 1; }

.link {
  text-decoration: none;
  color: #7acece; }
  .link:hover {
    color: #ffffff; }

.image-link:hover {
  opacity: 0.8; }

.social {
  display: flex;
  justify-content: flex-end; }
  .social__icon {
    display: block;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    margin-left: 5px;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #7acece;
    background-position: center; }
    .social__icon:hover {
      background-color: #ffffff; }
    @media (max-width: 767px) {
      .social__icon {
        height: 40px;
        width: 40px; } }
    .social__icon--git {
      background-image: url("../img/icon-github.png");
      background-size: 60px; }
    .social__icon--twitter {
      background-image: url("../img/twitter-256.png");
      background-size: 30px; }
    .social__icon--email {
      background-image: url("../img/mail-icon.png");
      background-size: 30px; }

.hidden {
  display: none; }

@media (max-width: 767px) {
  .hidden-mobile {
    display: none; } }

.books-form {
  width: 100%;
  padding: 20px;
  max-width: 400px;
  margin: 50px auto; }
  .books-form__search {
    color: #ffffff;
    margin: 0;
    text-align: center;
    outline: 0;
    border: 3px solid #7acece;
    background-color: #252525;
    width: 120%;
    left: -10%;
    position: relative;
    top: 10px;
    z-index: 2;
    font-size: 40px; }
  .books-form__suggestions {
    margin: 12px 0;
    padding: 0;
    position: relative; }
  .books-form__suggestion {
    color: #ffffff;
    background: #252525;
    list-style: none;
    border-bottom: 3px solid #000000;
    margin: 0;
    padding: 20px;
    transition: background 0.2s;
    display: flex;
    justify-content: space-between;
    text-transform: capitalize; }

.contact-form {
  display: flex;
  flex-direction: column;
  width: 100%; }
  .contact-form input,
  .contact-form textarea {
    color: #ffffff;
    background-color: #252525;
    border: 3px solid #7acece;
    padding: 5px;
    margin: 10px 0 15px;
    font-size: 14px; }
  .contact-form button {
    border: 0; }
